<template>
	<div class="pay-success-wrapper">
		<div class="pay-success-header">
			<div class="header-left">
				<span class="solution-top">产品购买</span>
			</div>
			<div class="header-right"></div>
		</div>
		<div class="pay-success-content">
			<div class="pay-success-left">
				<img src="../../assets/images/pay_success.png" />
				<div class="pay-tip">支付成功！</div>
			</div>
			<div class="pay-success-right">
				<div class="product-title-wrapper">
					<span class="product-title">您购买的产品为：</span>
					<span class="product-name">{{ productSeriesName }}</span>
				</div>
				<div class="product-content">
					<div
						v-for="(item, index) in product_arr"
						:key="index"
						class="product-item"
					>
						<div class="sn-wrapper">
							<span class="title">绑定的产品SN是：</span>
							<span class="code">{{ item.sn }}</span>
						</div>
						<div class="active-wrappper">
							<div class="avtive-area">
								<span class="title">您的激活码是：</span>
								<div>
									<p class="code" :id="'copy' + index">{{
									item.activationCodeNo
								}}</p>
								<span
								data-clipboard-action="copy"
								:data-clipboard-target="'#copy' + index"
								:class="'copy-' + index"
								class="copy"
								@click="copy(index)"
								>复制</span
							>
								</div>
								
							</div>

							
						</div>
					</div>
				</div>
				<div class="copy-tip">
					点击"复制"可复制您的激活码，用于激活您购买的产品
				</div>
			</div>
		</div>
		<div class="confirm-btn" @click="goHome">确定</div>
	</div>
</template>

<script>
import Clipboard from "clipboard";
import { getInfoByOrderNo } from "@/api/pay";
export default {
	data() {
		return {
			product_arr: [
			],
			productSeriesName: ""
		};
	},
	created() {},
	mounted() {
		this.init();
	},
	computed: {},
	methods: {
		copy(index) {
			var clipboard = new Clipboard(".copy-" + index);
			clipboard.on("success", (e) => {
				this.$message({
					message: "复制成功",
					type: "success",
				});
				// 释放内存
				clipboard.destroy();
			});
			clipboard.on("error", (e) => {
				// 释放内存
				clipboard.destroy();
				this.$message({
					message: "该浏览器不支持自动复制",
					type: "fail",
				});
			});
		},
		goHome() {
			this.$router.push("/");
		},
		init() {
			getInfoByOrderNo({
				orderId: this.$route.params.id,
			}).then((res) => {
				this.productSeriesName = res.data.data[0].productSeriesName
				this.product_arr = res.data.data;
			});
		},
	},
};
</script>

<style lang="scss" scoped>
.pay-success-wrapper {
	.pay-success-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #dddddd;
		padding-bottom: 15px;
		.header-left {
			background: url("../../assets/images/ico_navdot.png") no-repeat left 10px;
			.solution-top {
				display: block;
				font-size: 20px;
				line-height: 36px;
				font-family: "微软雅黑";
				padding-left: 23px;
				color: #3b3b3b;
			}
		}
		.header-right {
			display: flex;
			align-items: center;
			.el-input {
				width: 320px;
				margin-right: -4px;
				.el-input__inner {
					height: 36px;
				}
			}
			.el-button {
				width: 120px;
				z-index: 10;
				background-color: #3178bf;
				color: #fff;
				height: 36px;
				line-height: 36px;
				padding: 0;
				border: none;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}
	}
	.pay-success-content {
		display: flex;
		justify-content: center;
		margin-top: 50px;
		.pay-success-left {
			text-align: center;
			margin-right: 125px;
			.pay-tip {
				font-size: 34px;
				color: #3178bf;
			}
		}
		.pay-success-right {
			width: 620px;
			.product-title-wrapper {
				background-color: #f1f4f6;
				padding-left: 30px;
				height: 50px;
				display: flex;
				align-items: center;
				.product-title {
					font-size: 20px;
					color: #8c8c8c;
				}
				.product-name {
					font-size: 24px;
					font-weight: bold;
					color: #3178bf;
				}
			}
			.product-content {
				padding: 0 30px;
				border: 1px solid #e3e8ed;
				margin-top: 10px;
				.product-item {
					padding-bottom: 24px;
					margin-top: 24px;
				}
				.product-item:not(:last-child) {
					border-bottom: 1px solid #e3e8ed;
				}
				.active-wrappper {
					display: flex;
					align-items: center;
					justify-content: space-between;
				}
				.avtive-area {
					display: flex;
				}
				.title {
					font-size: 20px;
					color: #8c8c8c;
					text-align: right;
					display: inline-block;
					flex: 0 0 170px;
				}
				.code {
					flex: 1;
					font-size: 18px;
					color: #3c3e40;
					word-wrap: wrap;
    				word-break: break-all;
				}
				.copy {
					font-size: 18px;
					color: #3178bf;
					// margin-left: 15px;
					cursor: pointer;
				}
			}
			.copy-tip {
				font-size: 14px;
				color: #9aafb9;
				padding-left: 30px;
				margin-top: 5px;
			}
		}
	}
	.confirm-btn {
		width: 160px;
		height: 44px;
		line-height: 44px;
		color: #fff;
		background-color: #3178bf;
		border: 1px solid #f0f0f0;
		text-align: center;
		margin: 90px auto 0;
		border-radius: 4px;
		cursor: pointer;
	}
}
</style>
